import React from 'react';
import styled from 'styled-components';
import { useHistory } from 'react-router-dom';
import * as Requests from '../requests';
import { Movie } from '../types';

const Data = styled.div`
        text-align: center;
        /* display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center; */
        width: auto;
        height: 100vw; 
        
        /* position: relative; */
        /* top:0;
        bottom: 0;
        left: 0;
        right: 0; */
        
        margin: 1px;

        #submitButton {
            margin: 15px;
            width: 250px;
            background-color: olivedrab;

            :hover {
                background-color: lightpink;
            }
        }
`;

const StyledTable = styled.table`
  caption-side: top;
  border: none;
  border-collapse: collapse;
  width: 100vw;
  font-size: 2vw;


  th, td {
      border: none;
  }

  td {
    padding: 10px 20px;
  }

  #comments {
      text-align: left;
  }

  tbody tr {
    :nth-of-type(odd) {
      background-color: #efefef;
    }
    :nth-of-type(even) {
      background-color: #fefefe;
    }
    :hover {
      background-color: lightpink;
    }
  }
  thead > tr {
    background-color: #c2c2c2;
  }
  caption {
    font-size: 1vw;
    padding: 5px;
    font-weight: bold;
  }

`;


const Database = () => {

  const [movies, setMovies] = React.useState<Movie[]>([]);
  const [sortType, setSortType] = React.useState<keyof Movie>('title');
  const history = useHistory();

  React.useEffect(() => {
    Requests.getMovies()
      .then(sortMovies)
      .catch (err => console.log(err));
  })

  const sortMovies = (moviesParam: Movie[] = movies) => {

        const sorted = [...moviesParam].sort((a: Movie, b: Movie) => {
          if(b[sortType] > a[sortType]) return -1;
          else if(a[sortType] > b[sortType]) return 1;
          return 0
      });

      setMovies(sorted)
    }

return (
  <Data>
    <StyledTable>
      <caption><h1>Movies We've Seen</h1></caption>
      <thead>
        <tr>
          <th>Movie Title</th>
          <th>IMDB</th>
          <th>RTC</th>
          <th>MTC</th>
          <th>SMN</th>
          <th>Comments</th>
        </tr>
      </thead>
      <tbody>
        {movies.map((movie, index) => (
          <tr key={index} onClick={() => history.push(`/edit/${movie.title}`)}>
            <td>{movie.title}</td>
            <td>{movie.imdb}</td>
            <td>{movie.rtc}</td>
            <td>{movie.mtc}</td>
            <td>{movie.smn}</td>
            <td>{movie.comments}</td>
          </tr>
        ))}
      </tbody>
    </StyledTable>
    <br />
    <button onClick={() => history.push('/new')}><h2>Add New Movie</h2></button>
    <br />
    <a href="/"><i>Return To Main Page</i></a>
  </Data>
)
}

export default Database;